.LoginBar{

  border: solid 1px black;
  position: absolute;
  top: 10px;
  right: 30px;
  font-size: 18px;
  cursor: pointer;
  color: rgb(46, 5, 65);
  transition: color, right .22s ease-in;
  z-index: 100;
  background-color: magenta;
}
.LoginBar:hover {
  color: black;
}
.LoginBar.open {
  right: 250px;

}


.FormAuthLayer {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 80%;
  max-width: 240px;
  padding: 20px 10px;
  box-sizing: border-box;
  background: #b8c4cc;
  -webkit-transform: translateX(240px);
          transform: translateX(240px);
  transition: -webkit-transform   .22s ease-in;
  transition: transform   .22s ease-in;
  transition: transform   .22s ease-in, -webkit-transform   .22s ease-in;

  z-index: 90;
  opacity: 0.9;
}

.FormAuthLayer.close {
}

.FormAuthLayer .add_user{
  color: black;
 
  padding: 0 2px;
  
}

.FormAuthLayer .add_user:hover{
  color: magenta;
  
}
.FormAuthLayer .alarm{
  color: red;
  
}
.FormAuthLayer.open {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);

}
.Backdrop{
